import React, { Component } from "react";
class Fader extends Component {
  state = { value: 0 };
  render() {
    const { value } = this.state;
    return (
      <input
        type="range"
        min="1"
        max="127"
        step="1"
        value={value}
        className="fader"
        id="myRange"
        onChange={value => this.handleChange(value.target.value)}
      ></input>
    );
  }

  handleChange = newValue => {
    const { value: currValue } = this.state;
    const { identifier, socket } = this.props;
    let direction = "";
    if (newValue !== currValue) {
      if (newValue >= currValue) {
        direction = "up";
      } else if (newValue < currValue) {
        direction = "down";
      }
    }
    this.setState({ value: newValue });
    if (direction !== "") this.send(identifier + ":" + direction, socket);
  };

  componentDidMount() {
    const { socket } = this.props;
    socket.on("data", data => {
      this.handleData(data);
    });
  }

  handleData(data) {
    // console.log(data);
  }

  send = (data, socket) => {
    socket.emit("data", data);
  };
}

export default Fader;
