import React from "react";
import io from "socket.io-client";
import Layout from "./components/layout";
import { MpaIcon } from "./components/icon";

export class App extends React.Component {
  constructor() {
    super();
    this.state = {
      editing: false,
      port: 8001,
      endpoint: "http://" + window.location.hostname + ":" + 8001,
      data: {},
      ready: false
    };
  }

  socket;
  clientId;

  initSocket = () => {
    this.socket = io(this.state.endpoint);
    this.socket.on("connect", () => {
      this.init();
      this.socket.on("disconnect", reason => {
        console.log(`disconnected due to ${reason}`);
        this.setState({ ready: false, data: {} });
      });
    });
  };

  exitSocket = () => {
    ["data", "client-ready", "disconnect", "connect"].forEach(s => {
      this.socket.off(s);
    });
    this.socket.close();
  };

  componentDidMount() {
    this.initSocket();
  }

  request = () => {
    this.socket.emit("client");
  };

  init = () => {
    this.request();

    this.socket.on("client-ready", clientId => {
      this.clientId = clientId;
      this.socket.emit("data", this.clientId);
    });

    this.socket.on("data", data => {
      this.setState({ data, ready: true });
    });
  };

  handleUpdatePort = () => {
    if (this.state.editing) {
      this.setState({ editing: false });
      this.setState(
        {
          port: this.state.port,
          endpoint: "http://" + window.location.hostname + ":" + this.state.port
        },
        () => {
          console.log(`updating port to ${this.state.port}`);
          this.exitSocket();
          this.initSocket();
        }
      );
    } else {
      this.setState({ editing: true });
    }
  };

  handleUpdatePortTextChange = evt => {
    this.setState({ port: evt.target.value });
  };

  render() {
    if (this.state.ready) {
      return (
        <Layout
          data={this.state.data}
          socket={this.socket}
          clientId={this.clientId}
        ></Layout>
      );
    } else {
      return (
        <React.Fragment>
          <div style={{ display: "flex", justifyContent: "center" }}>
            <MpaIcon style={{ fontSize: 100 }}></MpaIcon>
          </div>
          <div style={{ display: "flex", justifyContent: "center" }}>
            <h1>Waiting For Your Tiles Hub To Come Online</h1>
          </div>
          <div style={{ display: "flex", justifyContent: "center" }}>
            <h2>Address: {window.location.hostname}</h2>
          </div>
          <div style={{ display: "flex", justifyContent: "center" }}>
            <h2>Port:</h2>
            <input
              onChange={this.handleUpdatePortTextChange}
              value={this.state.port}
              type="text"
              style={{
                marginLeft: "10px",
                marginRight: "10px",
                backgroundColor: "transparent",
                border: "1px solid white",
                color: "white",
                fontSize: "1.5em ",
                fontWeight: "bold",
                maxWidth: "100px",
                textAlign: "center",
                borderRadius: "10px"
              }}
              disabled={this.state.editing ? false : true}
            ></input>
            <button
              style={{
                marginLeft: "20px",
                backgroundColor: "transparent",
                border: "1px solid white",
                color: "white",
                fontSize: "1.5em ",
                fontWeight: "bold",
                maxWidth: "100px",
                borderRadius: "10px"
              }}
              onClick={this.handleUpdatePort}
            >
              {this.state.editing ? "DONE" : "EDIT"}
            </button>
          </div>
        </React.Fragment>
      );
    }
  }
}

export default App;
