import React, { Component } from "react";
import { MpaButton } from "./button";
import Encoder from "./encoder";
import Fader from "./fader";
import { MpaSlider } from "./slider";
import { MpaSwitch } from "./switch";
import { MpaSelector } from "./selector";

class Widget extends Component {
  constructor(props) {
    super(props);
    this.socket = props.socket;
    this.state = {
      type: props.meta.widget.type
    };
  }

  socket;

  componentDidMount() {
    this.socket.on("rx", data => {
      if (this.props.meta.widget.name === data.name) {
        if (data.kind === "widgetevent" && data.type === "TYPECHANGE") {
          console.log(this.props.meta.widget);
          this.setState({ type: data.val });
        }
      }
    });
  }

  widgetForMeta = m => {
    const { identifier } = this.props;
    const { widget: w } = m;
    if (this.state.type === "BUTTON") {
      return (
        <div className="box">
          <MpaButton
            key={this.widgetIdentifier(identifier, w.idx, this.state.type)}
            identifier={this.widgetIdentifier(
              identifier,
              w.idx,
              this.state.type
            )}
            meta={m}
            socket={this.socket}
          ></MpaButton>
        </div>
      );
    } else if (this.state.type === "ENCODER") {
      return (
        <div className="box">
          <Encoder
            key={this.widgetIdentifier(identifier, w.idx, this.state.type)}
            identifier={this.widgetIdentifier(
              identifier,
              w.idx,
              this.state.type
            )}
            socket={this.socket}
          ></Encoder>
        </div>
      );
    } else if (this.state.type === "FADER") {
      return (
        <Fader
          key={this.widgetIdentifier(identifier, w.idx, this.state.type)}
          identifier={this.widgetIdentifier(identifier, w.idx, this.state.type)}
          socket={this.socket}
        ></Fader>
      );
    } else if (this.state.type.startsWith("SLIDER")) {
      return (
        <div className="box">
          <MpaSlider
            key={w.name}
            identifier={w.name}
            meta={m}
            socket={this.socket}
            orientation={this.state.type.split("_")[1]}
          ></MpaSlider>
        </div>
      );
    } else if (this.state.type === "TOGGLE") {
      return (
        <div className="box">
          <MpaSwitch
            key={w.name}
            identifier={w.name}
            meta={m}
            socket={this.socket}
          ></MpaSwitch>
        </div>
      );
    } else if (this.state.type.startsWith("SELECTOR")) {
      return (
        <div className="box">
          <MpaSelector
            key={w.name}
            identifier={w.name}
            meta={m}
            socket={this.socket}
            orientation={this.state.type.split("_")[1]}
          ></MpaSelector>
        </div>
      );
    } else if (this.state.type === "EMPTY") {
      return <div className="box"></div>;
    } else {
      return <React.Fragment></React.Fragment>;
    }
  };

  handleChange = (name, val) => {
    console.log(name, val);
  };

  widgetIdentifier = (row, index, widget) => {
    return row + "-" + widget + "-" + index;
  };

  render() {
    const { meta } = this.props;
    return this.widgetForMeta(meta);
  }
}

export default Widget;
