import React from "react";
import Chain from "./chain";
import Widgets from "./widgets";

export class Layouter extends React.Component {
  render() {
    const { widgetRows, socket } = this.props;
    return (
      <Chain wrapperClasses="row-container">
        <Widgets socket={socket} widgets={widgetRows}></Widgets>
      </Chain>
    );
  }
}
