import React, { Component } from "react";
import { CircleSlider } from "react-circle-slider";
class Encoder extends Component {
  state = { value: 0, label: "abcdef" };
  render() {
    const { identifier } = this.props;
    const { value, label } = this.state;
    return (
      <React.Fragment>
        {/* <CircleSlider
          id={identifier}
          key={identifier}
          value={value}
          size={60}
          stepSize={1}
          knobRadius={8}
          circleWidth={5}
          progressWidth={5}
          className="circleSlider"
          onChange={this.handleChange}
          circleColor="black"
        ></CircleSlider> */}
        <label>
          {label}:{value}
        </label>
      </React.Fragment>
    );
  }

  componentDidMount() {
    const { socket } = this.props;
    socket.on("data", data => {
      this.handleData(data);
    });
  }

  handleData(data) {
    if (data.startsWith("!")) {
      //simple protocol to match glue on the either
      const incomingData = data.split("!")[1];
      const incomingId = incomingData.split(":")[0];
      const property = incomingData.split(":")[1];
      const { identifier } = this.props;
      if (incomingId === identifier) {
        const what = property.split("=")[0];
        const whatValue = property.split("=")[1];
        switch (what) {
          case "label":
            const label = whatValue;
            this.setState({ label });
            break;
          case "value":
            const { value: currValue } = this.state;
            const value = whatValue;
            if (currValue !== whatValue) this.setState({ value });

            break;
          default:
            break;
        }
      }
    }
  }

  handleChange = newValue => {
    const { value: currValue } = this.state;
    const { identifier, socket } = this.props;
    let direction = "";
    if (newValue !== currValue) {
      if (newValue >= currValue) {
        direction = "right-1";
      } else if (newValue < currValue) {
        direction = "left-1";
      }
    }
    if (direction !== "") this.send(identifier + ":" + direction, socket);
  };

  send = (data, socket) => {
    socket.emit("data", data);
  };
}

export default Encoder;
